import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import { ConfigProvider, AdaptivityProvider, AppRoot } from '@vkontakte/vkui'

import Home from './panels/Home';
import Shop from './panels/Shop';
import Info from './panels/Info';

const App = () => {
	const [activePanel, setActivePanel] = useState("home"); // –°—Ç–∞–≤–∏–º –Ω–∞—á–∞–ª—å–Ω—É—é –ø–∞–Ω–µ–ª—å
	const [history] = useState(['home']) // –ó–∞–Ω–æ—Å–∏–º –Ω–∞—á–∞–ª—å–Ω—É—é –ø–∞–Ω–µ–ª—å –≤ –º–∞—Å—Å–∏–≤ –∏—Å—Ç–æ—Ä–∏–π.
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [properties, setProperties] = useState([{ vk: undefined, balance: undefined, rub: undefined, points: undefined, alert: undefined, vip: {} }])
	const API_KEY = "C4D52C8426DAC";

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			const api_url = await fetch(`https://likebot.ru/data?token=${API_KEY}&id=${user.id}`);
			const data = await api_url.json();
			setPopout(null); // –æ–±–Ω—É–ª–µ–Ω–∏–µ —Å–ø–∏–Ω–µ—Ä–∞


			setProperties({
				vk: data.vk,
				balance: data.balance,
				rub: data.rub,
				points: data.points,
				alert: data.alert,
				vip: data.vip
			});
		}

		fetchData();
		window.addEventListener('popstate', () => goBack()); // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è –±—Ä–∞—É–∑–µ—Ä–Ω–æ–π –∏—Å—Ç–æ—Ä–∏–∏
	}, [properties]);

	// –ù–∞–≤–∏–≥–∞—Ü–∏—è:
	const goBack = () => {
		if (history.length > 1) { // –ï—Å–ª–∏ –≤ –º–∞—Å—Å–∏–≤–µ –±–æ–ª—å—à–µ –æ–¥–Ω–æ–≥–æ –∑–Ω–∞—á–µ–Ω–∏—è:
			history.pop() // —É–¥–∞–ª—è–µ–º –ø–æ—Å–ª–µ–¥–Ω–∏–π —ç–ª–µ–º–µ–Ω—Ç –≤ –º–∞—Å—Å–∏–≤–µ.
			setActivePanel(history[history.length - 1]) // –ò–∑–º–µ–Ω—è–µ–º –º–∞—Å—Å–∏–≤ —Å –∏—Å—Ç–æ—Ä–∏–µ–π –∏ –º–µ–Ω—è–µ–º –∞–∫—Ç–∏–≤–Ω—É—é –ø–∞–Ω–µ–ª—å.
		}
	}

	function goToPage(name) { // –í –∫–∞—á–µ—Å—Ç–≤–µ –∞—Ä–≥—É–º–µ–Ω—Ç–∞ –ø—Ä–∏–Ω–∏–º–∞–µ–º id –ø–∞–Ω–µ–ª–∏ –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥–∞
		window.history.pushState({ panel: name }, name); // –°–æ–∑–¥–∞—ë–º –Ω–æ–≤—É—é –∑–∞–ø–∏—Å—å –≤ –∏—Å—Ç–æ—Ä–∏–∏ –±—Ä–∞—É–∑–µ—Ä–∞
		setActivePanel(name); // –ú–µ–Ω—è–µ–º –∞–∫—Ç–∏–≤–Ω—É—é –ø–∞–Ω–µ–ª—å
		history.push(name); // –î–æ–±–∞–≤–ª—è–µ–º –ø–∞–Ω–µ–ª—å –≤ –∏—Å—Ç–æ—Ä–∏—é
	};


	return (
		<ConfigProvider isWebView={true}>
			<AdaptivityProvider >
				<AppRoot>
					<View activePanel={activePanel}
						popout={popout}
						history={history}
						onSwipeBack={goBack} >
						<Home id='home' fetchedUser={fetchedUser} properties={properties} goToPage={goToPage} />
						<Shop id='shop' properties={properties} goToPage={goToPage} />
						<Info id='info' properties={properties} />
					</View>
				</AppRoot>
			</AdaptivityProvider>
		</ConfigProvider>

	);
}

export default App;

